<h1 id="introduction" tabindex="-1"><a class="header-anchor" href="#introduction" aria-hidden="true">#</a> Introduction</h1>
<p><code>Command Line Interface</code>就是<code>命令行接口</code>，它可以配置你的开发并与之交互。在早期的原型设计和分析中非常有用。在大多数情况下，<code>CLI</code>通常被用来配置一些文件和标志一些启动进程。</p>
<p>为了正确使用和方便分发此配置，<code>webpack</code>可以使用<code>webpack.config.js</code>来配置。任何发送到<code>CLI</code>的参数都将<code>映射</code>到配置文件中的参数。</p>
